/* src/App.css */

html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  overflow: auto;
  font-family: "Helvetica Neue", "Helvetica", "Hiragino Sans", "Hiragino Kaku Gothic ProN", "Arial", "Yu Gothic", "Meiryo", sans-serif;
}

.App {
  text-align: center;
  margin: 0;
  padding: 0;
  position: relative;
}

.hamburger-menu {
  position: fixed;
  top: 10px;
  right: 0px;
  z-index: 3;
  text-align: right;
  text-align: -webkit-right;
}

.hamburger-button {
  background: none;
  border: none;
  color: gray;
  font-size: 36px;
  cursor: pointer;
  text-align: right;
  text-align: -webkit-right;
}

.hamburger-button.white {
  color: white;
}

.menu-items {
  position: fixed;
  top: 0;
  right: 0;
  height: 100%;
  width: 30%;
  background-color: rgba(70, 130, 180, 0.8);
  transform: translateX(100%);
  transition: transform 0.3s ease-in-out;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  z-index: 2;
}

.menu-items.show {
  transform: translateX(0);
}

.menu-item {
  margin: 15px 0;
}

.menu-items a {
  color: white;
  text-decoration: none;
  font-size: 18px;
  line-height: 2;
  margin: 10px 0;
}

.menu-items a:hover {
  text-decoration: underline;
}

.video-section {
  position: relative;
  height: 90vh;
  width: 100%;
  overflow: hidden;
}

.background-video {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 90%;
  max-width: 720px; /* Original video width */
  height: auto;
  max-height: 1280px; /* Original video height */
  transform: translate(-50%, -50%);
  z-index: -1;
}

.scroll {
  display: inline-block;
  padding-top: 70px;
  position: relative;
}
.scroll::before {
  animation: scroll 3.5s infinite;
  border: solid #000;
  border-width: 0 0 1px 1px;
  content: "";
  display: inline-block;
  margin: auto;
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  transform: rotate(-45deg);
  width: 20px;
  height: 20px;
}
@keyframes scroll {
  0% {
    transform: rotate(-45deg) translate(0, 0);
  }
  80% {
    transform: rotate(-45deg) translate(-30px, 30px);
  }
  0%, 80%, 100% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
}

.content-profile {
  position: relative;
}

.profile {
  position: relative;
  height: 100vh;
  width: 100%;
  overflow: hidden;
  margin-bottom: 0vh; /* セクション間のスペースを確保 */
}

.profile-video {
  position: absolute;
  top: 40%;
  left: 50%;
  /* width: 100%;
  height: 100%; */
  max-height: 80vh; /* 動画の最大高さを画面の高さに設定 */
  object-fit: cover; /* 動画がセクション全体をカバーするように */
  transform: translate(-50%, -50%);
  z-index: -1; /* テキストを上に表示 */
  border-radius: 15px; /* 角を丸くする */
}

.text-overlay {
  position: absolute;
  bottom: 30%;
  left: 25%;
  color: #f1f1f1;
  background: rgba(0, 0, 0, 0.0);
  padding: 10px;
  border-radius: 8px;
  font-size: 12px;
  text-align: left;
  max-width: fit-content;
  z-index: 1; /* テキストを動画の上に表示 */
}

.content {
  position: relative;
  /* height: 70vh; */
  width: 100%;
  overflow: hidden;
}

.program {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

.program .content-box {
  max-width: 80%;
  margin: auto; /* 中央揃え */
  padding: 20px; /* 内側の余白を追加 */
  background-color: rgba(255, 255, 255, 0.8); /* 背景色と透明度を追加 */
  border-radius: 10px; /* 角を丸める */
}

.image-cursor {
  cursor: pointer;
  max-width: 100%;
  height: auto;
}

.program-schedule {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 10vh; /* セクション間のスペースを確保 */
}

.program-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 10px 0;
}

.program-time {
  font-size: 15px;
}

.program-event {
  font-size: 16px;
}

.program-info {
  font-size: 14px;
}

.content-message {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  margin-bottom: 0vh; /* セクション間のスペースを確保 */
}

.footer {
  background-color: #f1f1f1;
  padding: 10px 0;
  text-align: center;
}

.footer-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer-content p {
  margin: 0;
  color: #666;
  font-size: 10px;
}

/* スマホ画面用のレスポンシブデザイン */
@media (max-width: 768px) {
  .video-section {
    position: relative;
    height: 95vh;
    width: 100%;
    overflow: hidden;
  }

  .background-video {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 95%;
    max-width: 720px; /* Original video width */
    height: auto;
    max-height: 1280px; /* Original video height */
    transform: translate(-50%, -50%);
    z-index: -1;
  }

  .profile-video {
    position: absolute;
    top: 50%;
    left: 50%;
    /* width: 100%;
    height: 100%; */
    max-height: 80vh; /* 動画の最大高さを画面の高さに設定 */
    object-fit: cover; /* 動画がセクション全体をカバーするように */
    transform: translate(-50%, -50%);
    z-index: -1; /* テキストを上に表示 */
    border-radius: 15px; /* 角を丸くする */
  }

  .text-overlay {
    position: absolute;
    bottom: 12%;
    left: 10%;
    color: #f1f1f1;
    background: rgba(0, 0, 0, 0.0);
    padding: 10px;
    border-radius: 8px;
    font-size: 12px;
    text-align: left;
    max-width: fit-content;
    z-index: 1; /* テキストを動画の上に表示 */
  }
}

/* デバイスの画面がランドスケープ（横向き）モード */
@media only screen and (orientation: landscape) {
  .menu-items {
    width: 40%;
  }

  .menu-items a {
    font-size: 16px;
    margin: 5px 0;
  }

  .content-box {
    max-width: 80%;
  }

  .background-video {
    width: auto;
    height: 100%;
  }

  .text-overlay {
    position: absolute;
    bottom: 12%;
    left: 39%;
    color: #f1f1f1;
    background: rgba(0, 0, 0, 0.0);
    padding: 10px;
    border-radius: 8px;
    font-size: 12px;
    text-align: left;
    max-width: fit-content;
    z-index: 1; /* テキストを動画の上に表示 */
  }
}
